
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import api from "@/api/api";

export default {
components: {
Group,
XInput,
Selector,
XButton
},
data() {
return {
phone: "",
password: "",
password2: "",
gender: "女",
nickname: ""
};
},
methods: {
async userRegister() {
if (this.phone.length != 11) {
this.$vux.toast.text("手机号不正确", "top");
return;
}
if (this.password != this.password2) {
this.$vux.toast.text("密码输入不一致", "top");
return;
}
if (this.$R.empty(this.password)) {
this.$vux.toast.text("密码不允许为空", "top");
return;
}

let user = this.$R.clone(this.$data);
delete user.password2;

let res = await api.user.reg(user);

if (res.success) {
this.$vux.alert.show({
title: "操作提示",
content: "注册成功，去登录吧",
buttonText: "去登录",
onHide:()=>{
this.$router.push({ name: "userLogin" });
}
});
} else {
this.$vux.alert.show({
title: "操作提示",
content: res.msg,
onShow() {
console.log("Plugin: I'm showing");
},
onHide() {
console.log("Plugin: I'm hiding");
}
});
}
}
},
created() {

}
};



