
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'

import api from "@/api/api";

export default {
components: {
Flexbox,
FlexboxItem,
Divider,
Group,
XInput,
Selector,
XButton
},
data() {
return {
phone: "",
password: ""
};
},
methods: {
toReg() {
this.$router.push({ name: "userReg" });
},
async userLogin() {
if (this.phone.length != 11) {
this.$vux.toast.text("手机号不正确", "top");
return;
}
if (this.$R.empty(this.password)) {
this.$vux.toast.text("密码不允许为空", "top");
return;
}
this.$vux.loading.show({ text: "登录中..." });
let res = await api.user.login(this.phone, this.password);
this.$vux.loading.hide();
if (res.success) {

this.$ls.set("userid", res.data.id);
this.$ls.set("id32", res.data.id32);
this.$ls.set("phone", res.data.phone);

this.$router.push({
name: "userDetail",
query: { id32: res.data.id32 }
});
} else {
this.$vux.alert.show({
title: "操作提示",
content: res.msg
});
}
}
},
created() {
console.log(111);
}
};



